.switcher::before {
  display: block;
  width: 40px;
  height: 20px;
  background-color: rgba(236, 169, 246, 0.756);
  border-radius: 10px;
  content: '';
  position: absolute;
  left: 0;
}

.switcher {
  position: relative;
  padding-left: 60px;
  user-select: none;
  cursor: pointer;
}

.switcher::after {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  content: '';
  display: block;
  background-color: aliceblue;
  position: absolute;
  left: 5px;
  top: 5px;
  transition: all 0.2s ease-in;
}

#gender:checked+.switcher::after {
  left: 25px;
}

#gender:checked+.switcher::before {
  background-color: rgb(0, 166, 255);
}