.form {
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: 0 auto;
  gap: 1.5rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.inputs {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.hidden {
  display: none;
}

input {
  -webkit-box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  -moz-box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  border: none;
  outline: none;
  height: 2rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  border-radius: 4px;
  font-size: 13px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.submit:disabled {
  background-color: rgba(174, 37, 216, 0.19);
}

select {
  -webkit-box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  -moz-box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  border: none;
  outline: none;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  height: 2rem;
  border-radius: 4px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.personal-data {
  font-size: 12px;
}

.country {
  padding-right: 1rem;
}

.confirm {
  color: rgb(3, 223, 3);
  font-size: 1.2rem;
  text-align: center;
  margin-bottom: 20px;
}