.product {
  width: 23%;
  display: flex;
  margin-top: 20px;
  gap: 5px;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  -webkit-box-shadow: 0px 0px 16px 4px rgba(174, 37, 216, 0.19);
  -moz-box-shadow: 0px 0px 16px 4px rgba(174, 37, 216, 0.19);
  box-shadow: 0px 0px 16px 4px rgba(174, 37, 216, 0.19);
}

.product-name {
  margin: 0 5px;
}

.product-img {
  width: 100%;
}

.product-brand {
  height: 20px;
  margin: 0 5px;
  padding-right: 10px;

}

.product .product-sizes {
  display: flex;
  gap: 7px;
  margin: 0 5px;

}

.product .product-price {
  font-weight: 600;
  margin: 0 5px 10px;
}

.product-add .product-type {
  font-size: 12px;
}

.products-wrapper {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 30px;
}

.product-checked {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1;
  height: 20px;
  display: none;
}

.product-make_category {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  margin: 0 5px;
}

.product-buy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
}

.card-list {
  display: flex;
  justify-content: center;
  gap: 1%;
  row-gap: 15px;
  flex-wrap: wrap;
}