.header {
  background-color: rgba(236, 169, 246, 0.756);
  -webkit-box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  -moz-box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  box-shadow: 0px 0px 16px 6px rgba(174, 37, 216, 0.19);
  margin-bottom: 2rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

a {
  text-decoration: none;
}

.active {
  border-bottom: 2px solid white;
}

.header {
  display: flex;
  justify-content: flex-start;
  padding: 2rem;
}

.header-list {
  display: flex;
  gap: 4rem;
  list-style: none;
}