.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 4rem;
}

.current-card {
  color: rgb(230, 186, 251);
  margin-top: 20px;
  margin-bottom: 20px;
}

.modal-window {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 200px;
  height: 300px;
  background-color: rgb(230, 186, 251);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  overflow: hidden;
  padding: 10px;
}

.modal-window-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(128, 128, 128, 0.302);
  cursor: pointer;
}

.close {
  position: absolute;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  left: 90%;
  top: 3%;
  cursor: pointer;
}