.personal-data::before {
  display: block;
  width: 20px;
  height: 20px;
  border: 1px solid rgba(128, 128, 128, 0.501);
  content: '';
  position: absolute;
  left: 0;
}

.personal-data {
  position: relative;
  padding-left: 60px;
  user-select: none;
  cursor: pointer;
}

.personal-data::after {
  width: 15px;
  height: 7px;
  content: '';
  display: block;
  border-left: 2px solid transparent;
  border-bottom: 2px solid transparent;
  transform: rotate(-45deg);
  position: absolute;
  left: 3px;
  top: 5px;
  transition: all 0.2s ease-in;
}

#personal:checked+.personal-data::after {
  border-color: rgba(128, 128, 128, 0.501);
}